export default {
	data() {
		return {
			onlywork:[],
			stute:""
		}
	},
	onLoad(option) {
	        
			var that = this;
	  
		uni.request({
			
			url: 'http://106.13.239.202/index.php?c=workorder&m=onlywork',
			method: 'POST',
			header: {"content-type": "application/x-www-form-urlencoded"}, 
			data: {
				code:option.code
			},
			success: res => {
			
			  console.log(res.data)
			  that.onlywork = res.data.data;
			  if(that.onlywork[0].stute==0)
			  {
				  that.stute = "待处理";
			  }
			  else if(that.onlywork[0].stute==1)
			  {
				  that.stute = "已完成";
			  }
			},
			fail: () => {
			
			},
			complete: () => {}
		});
		
		
	}
	,
	methods: {
		
	}
}
